html {
  box-sizing: border-box;
}

body, html {
  height: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden;
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

.main {
  flex: 1 1 auto;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.logo {
  width: 53px;
  height: 53px;
}

.btn {
  position: relative;
  z-index: 10;
  padding: 16.17px 24.26px;
  border-radius: 20px;
  display: inline-block;
  font-family: "Inter";
  font-size: 16.172px;
  font-weight: 600;
  line-height: 150%;
  text-transform: uppercase;
}
.btn--fill {
  background-color: #141416;
  border: 1px solid #141416;
  color: #fff;
  transition: all 0.2s ease-in;
}
.btn--fill svg {
  transition: fill 0.2s ease;
}
@media (any-hover: hover) {
  .btn--fill:hover {
    background-color: #fff;
    color: #141416;
  }
  .btn--fill:hover svg {
    fill: #141416;
  }
}
.btn--fill.btn--white {
  background-color: #fff;
  color: #141416;
}
.btn--stroke {
  border: 1px solid #141416;
  color: #141416;
  transition: background-color 0.2s ease-in, color 0.2s ease-in;
}
@media (any-hover: hover) {
  .btn--stroke:hover {
    background-color: #141416;
    color: #fff;
  }
}
.btn--stroke.btn--white {
  border: 2px solid #fff;
  color: #fff;
}
.btn--sort {
  border: 2.2px solid #141416;
  color: #141416;
  padding: 17px;
  background-position: 17px, center;
  background-size: 25px 25px;
  background-repeat: no-repeat;
  transition: border-style 0.2s ease;
}
@media (any-hover: hover) {
  .btn--sort:hover {
    background-color: transparent;
    color: #141416;
    border-style: dashed;
  }
}
.btn--category {
  padding-left: 42px;
  background-image: url("../img/list.svg");
}
.btn--collection {
  padding-left: 42px;
  background-image: url("../img/voice.svg");
}
.btn--price {
  padding-left: 42px;
  background-image: url("../img/dollar.svg");
}

.container-left {
